.footer-page {
  background-color: $gray;
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: -40px;
  z-index: 10;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }
}

.footer-page__wrapper {
  max-width: 1300px;
  box-sizing: border-box;
  text-align: center;
  padding-left: 19px;
  padding-right: 20px;
  padding-top: 40px;
  padding-bottom: 15px;
  width: 100%;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    padding-left: 32px;
    padding-right: 30px;
    padding-top: 55px;
    padding-bottom: 47px;
    align-items: center;
    text-align: start;
  }

  @media (min-width: $desktop-width) {
    padding-left: 43px;
    padding-right: 36px;
    padding-top: 63px;
    padding-bottom: 50px;
  }
}

.footer-page__wrapper--form {
  @media (min-width: $desktop-width) {
    padding-top: 73px;
  }
}

.footer-page__logo {
  margin: 0 auto;
  padding-bottom: 40px;
  margin-bottom: 26px;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 0;
  }
}

.footer-page__logo-image {
  margin-bottom: 26px;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding: 0;
  }
}

.footer-page__HTML-Academy {
  display: flex;
  justify-content: space-between;
}

.footer-page__HTML-link {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: $text-color;
  text-decoration: none;
  width: 91%;
  text-align: start;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    width: 165px;
  }
}

.footer-page__HTML-icon {
  cursor: pointer;
  width: 37px;
  margin-top: -35px;
  margin-left: 97%;

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin-left: 129px;
  }
}

.footer-page__HTML-link svg path {
  pointer-events: all;
  fill: #666666;
}

.footer-page__HTML-link:hover svg path {
  fill: $btn-color;
}

.footer-page__logo-image:active {
  opacity: 0.6;
}
